.footer {
  background-color: #252324;
  height: 736px;
  padding-top: 110px;
  &__logo {
    font-family: Anglecia Pro Display;
    font-size: 70px;
    letter-spacing: 0.45em;
    color: var(--color-white);
    margin-bottom: 58px;
  }
  &__nav-item {
    &:not(:last-child) {
      margin-bottom: 17px;
    }
  }
  &__nav-link {
    font-size: 18px;
    color: var(--color-white);
  }
  &__contacts-title {
    font-size: 18px;
    color: var(--color-white);
    font-weight: 400;
    margin-bottom: 19px;
  }
  &__descr {
    color: var(--color-footer-gray);
  }
  &__phone {
    margin-top: 42px;
  }
  &__title {
    font-size: 41px;
    font-weight: 500;
    color: var(--color-white);
    margin-bottom: 60px;
    margin-top: 25px;
  }
  &__form-input {
    padding-bottom: 6px;
    color: var(--color-white);
    font-size: 18px;
    border: none;
    border-bottom: 1px solid var(--color-footer-gray);
    background-color: transparent;
    outline: none;
    transition: 0.3s;
    width: 480px;
  }
  &__form-group:not(:nth-child(3)) {
    position: relative;
    margin-bottom: 55px;
  }
  &__button {
    font-size: 18px;
    margin-top: 60px;
    margin-bottom: 27px;
    color: var(--color-black);
    background-color: #fff;
    border: none;
  }
  &__policy {
    color: var(--color-footer-gray);
    font-size: 12px;
  }
  &__policy-link {
    color: var(--color-white);
    font-size: 12px;
  }
  &__yo {
    margin-top: 117px;
    color: var(--color-white);
    font-size: 12px;
  }
}



input::placeholder {
  color: var(--color-footer-gray);
  font-size: 18px;
  font-weight: 400;
}

// ====================================================

.swiper-container {
  width: 600px;
  height: 300px;
}

