.location{
  margin-bottom: 190px;
  &__article {
    margin-bottom: 84px;
  }
  &__title {
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 6px;
  }
  &__subtitle {
    font-size: 18px;
    color: var(--color-sign);
  }
  &__descr {
    width: 436px;
  }
}

.map {
  height: 100vh;
  &__img {
  // width: 1600px;
  // height: 650px;
  height: 85%;
  background-image: url("../img/map.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  // margin-top: 84px;
}
}

.infrastructure {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  &__item {
    color: var(--color-sign);
    font-size: 16px;
  }
}