.about {
  display: flex;
  padding: 200px 0;
  justify-content: center;
    align-items: center;
  &__article {
    padding: 140px 0;
    width: 470px;
  }
}

.features {
  display: flex;
  justify-content: space-between;
  width: 330px;
  &__item {
    margin-top: 76px;
  }
  &__number {
    font-size: 41px;
    margin-bottom: 3px;
    
  }
  &__descr {
    font-size: 16px;
    color: var(--color-sign);
  }
}