.style {
  margin-bottom: 200px;
  &__img {
    width: 500px;
    height: 730px;
  }
  &__article {
    width: 495px;
    padding: 130px 0;
  }
  &__slide {
    height: 720px;
    // min-height: 100vh;
    background-image: url("../img/style__slider.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  &__container {
    display: flex;
    justify-content: space-between;
    margin-top: 46px;
    margin-bottom: 394px;
  }
  &__btn {
    display: flex;
    justify-content: space-between;
    width: 66px;
  }
  &__arrow-left {
    svg {
      width: 8px;
      height: 12px;
    }
  }
  &__arrow-right {
    svg {
      width: 8px;
      height: 12px;
    }
  }
}