@import url(../../node_modules/swiper/swiper-bundle.css);
:root {
  --color-black: #333;
  --color-white: #fff;
  --color-sign: #767676;
  --color-footer-gray: #949494;
}

@font-face {
  font-family: "AngleciaProDisplay";
  src: url("../fonts/AngleciaProDisplay-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "SF";
  src: url("../fonts/SF-Pro-Display-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "SF";
  src: url("../fonts/SF-Pro-Display-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "SF";
  src: url("../fonts/SF-Pro-Display-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "SF";
  src: url("../fonts/SF-Pro-Display-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

