html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "SF", sans-serif;
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

p {
  margin: 0;
  color: var(--color-black);
}

h1, h2, h3, h4 {
  margin: 0;
  color: var(--color-black);
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0;
  max-width: 1200px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 60px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 100;
  padding-top: 62px;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.phone {
  font-size: 18px;
  font-weight: 300;
  margin-right: 92px;
  color: var(--color-white);
}

.button {
  font-size: 18px;
  cursor: pointer;
  padding: 15px 40px;
}

.button--bd {
  border: 1px solid var(--color-white);
  background: transparent;
  color: var(--color-white);
}

.button--bd2 {
  border: 1px solid var(--color-black);
  background: transparent;
  color: var(--color-black);
}

.nav__list {
  display: flex;
}

.nav__item:first-child {
  margin-right: 80px;
}

.nav__link {
  color: var(--color-white);
  font-size: 18px;
}

.hero {
  min-height: 100vh;
  background-image: url("../img/Hero-bg.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.hero__container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero__content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hero__title {
  font-family: AngleciaProDisplay, serif;
  font-weight: 400;
  font-size: 118px;
  letter-spacing: 0.5em;
  color: var(--color-white);
}

.hero__descr {
  margin-top: 13px;
  font-size: 18px;
  color: var(--color-white);
}

.about {
  display: flex;
  padding: 200px 0;
  justify-content: center;
  align-items: center;
}

.about__article {
  padding: 140px 0;
  width: 470px;
}

.features {
  display: flex;
  justify-content: space-between;
  width: 330px;
}

.features__item {
  margin-top: 76px;
}

.features__number {
  font-size: 41px;
  margin-bottom: 3px;
}

.features__descr {
  font-size: 16px;
  color: var(--color-sign);
}

.title {
  font-size: 41px;
  font-weight: 500;
}

.title--max {
  margin-bottom: 86px;
}

.title--min {
  margin-bottom: 53px;
}

.descr {
  font-size: 18px;
}

.descr--indent {
  margin-bottom: 38px;
}

.img--max {
  width: 570px;
  height: 830px;
}

.img--min {
  width: 570px;
  height: 550px;
}

.location {
  margin-bottom: 190px;
}

.location__article {
  margin-bottom: 84px;
}

.location__title {
  font-weight: 600;
  font-size: 18px;
  margin-bottom: 6px;
}

.location__subtitle {
  font-size: 18px;
  color: var(--color-sign);
}

.location__descr {
  width: 436px;
}

.map {
  height: 100vh;
}

.map__img {
  height: 85%;
  background-image: url("../img/map.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.infrastructure {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
}

.infrastructure__item {
  color: var(--color-sign);
  font-size: 16px;
}

.advantage {
  margin-bottom: 195px;
}

.advantage__slider {
  display: flex;
}

.advantage__img {
  width: 570px;
  height: 700px;
}

.advantage__img:not(:last-child) {
  margin-right: 60px;
}

.advantage__descr {
  margin: 48px 0;
}

.style {
  margin-bottom: 200px;
}

.style__img {
  width: 500px;
  height: 730px;
}

.style__article {
  width: 495px;
  padding: 130px 0;
}

.style__slide {
  height: 720px;
  background-image: url("../img/style__slider.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.style__container {
  display: flex;
  justify-content: space-between;
  margin-top: 46px;
  margin-bottom: 394px;
}

.style__btn {
  display: flex;
  justify-content: space-between;
  width: 66px;
}

.style__arrow-left svg {
  width: 8px;
  height: 12px;
}

.style__arrow-right svg {
  width: 8px;
  height: 12px;
}

.apartments {
  margin-bottom: 200px;
}

.apartments__article {
  padding: 105px 0;
  width: 436px;
}

.apartments__descr {
  margin-bottom: 77px;
}

.comfort-zone {
  margin-top: 400px;
  margin-bottom: 385px;
}

.comfort-zone__descr {
  width: 436px;
}

.comfort-zone__article {
  margin-bottom: 86px;
}

.comfort-zone__slide {
  height: 100%;
  width: 100%;
}

.specifications {
  margin-bottom: 240px;
}

.specifications__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: space-between;
  grid-row-gap: 110px;
}

.specifications__item {
  width: 247px;
}

.specifications__title {
  font-weight: 600;
  font-size: 18px;
  margin-bottom: 20px;
}

.specifications__brr {
  width: 286px;
}

.specifications__headline {
  margin-bottom: 106px;
}

.footer {
  background-color: #252324;
  height: 736px;
  padding-top: 110px;
}

.footer__logo {
  font-family: Anglecia Pro Display;
  font-size: 70px;
  letter-spacing: 0.45em;
  color: var(--color-white);
  margin-bottom: 58px;
}

.footer__nav-item:not(:last-child) {
  margin-bottom: 17px;
}

.footer__nav-link {
  font-size: 18px;
  color: var(--color-white);
}

.footer__contacts-title {
  font-size: 18px;
  color: var(--color-white);
  font-weight: 400;
  margin-bottom: 19px;
}

.footer__descr {
  color: var(--color-footer-gray);
}

.footer__phone {
  margin-top: 42px;
}

.footer__title {
  font-size: 41px;
  font-weight: 500;
  color: var(--color-white);
  margin-bottom: 60px;
  margin-top: 25px;
}

.footer__form-input {
  padding-bottom: 6px;
  color: var(--color-white);
  font-size: 18px;
  border: none;
  border-bottom: 1px solid var(--color-footer-gray);
  background-color: transparent;
  outline: none;
  transition: 0.3s;
  width: 480px;
}

.footer__form-group:not(:nth-child(3)) {
  position: relative;
  margin-bottom: 55px;
}

.footer__button {
  font-size: 18px;
  margin-top: 60px;
  margin-bottom: 27px;
  color: var(--color-black);
  background-color: #fff;
  border: none;
}

.footer__policy {
  color: var(--color-footer-gray);
  font-size: 12px;
}

.footer__policy-link {
  color: var(--color-white);
  font-size: 12px;
}

.footer__yo {
  margin-top: 117px;
  color: var(--color-white);
  font-size: 12px;
}

input::placeholder {
  color: var(--color-footer-gray);
  font-size: 18px;
  font-weight: 400;
}

.swiper-container {
  width: 600px;
  height: 300px;
}
