.button {
  font-size: 18px;
  cursor: pointer;
  padding: 15px 40px;
  &--bd {
    border: 1px solid var(--color-white);
    background: transparent;
    color: var(--color-white);
  }
  &--bd2 {
    border: 1px solid var(--color-black);
    background: transparent;
    color: var(--color-black);
  }
}