.specifications {
  margin-bottom: 240px;
  &__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-content: space-between;
    grid-row-gap: 110px;
  }
  &__item {
    width: 247px;
  }
  &__title {
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 20px;
  }
  &__brr {
    width: 286px;
  }
  &__headline {
    margin-bottom: 106px;
  }
}